.side-container {
  background: var(--light-blue);

  max-width: 1024px;

  width: 50%;
  min-height: 500px;

  display: flex;
  flex-direction: column;
  flex: 1;

  border-radius: 15px;
  border: 5px solid grey;

  background: white;
}

@media (max-width: 768px) {
  .side-container {
    width: 100%;
    height: 100%;
  }

  .side-cat img {
    max-width: 100px !important;
  }
}

.side-cat {
  display: flex;
  flex: 1;

  justify-content: center;
  align-items: center;

  margin-bottom: var(--main-padding);
}

.side-cat img {
  max-width: 150px;
}